*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    width: 100%;
    min-height: 100vh;
    overflow-x: hidden;
    background-color: rgb(217, 230, 241);
    
}

.row{
    border: 2px solid black;
    border-radius: 20px;
    box-shadow: 2px 2px 10px gray;
}

.bmi-img{
    width: 100%;
    height: 100%;
    background-image:  url('../asset/bmiimgs.jpeg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.error{
    font-size: 14px;
    color: red;
}
.result{
    border: 2px solid rgb(226, 70, 13);
    border-radius: 10px;
}